@font-face {
  font-family: "Oswald";
  font-style: normal;
  font-weight: 400;
  font-display: swap;
  src:
    url("../fonts/oswaldregular.woff2") format("woff2"),
    url("../fonts/oswaldregular.woff") format("woff");
}

@font-face {
  font-family: "Oswald";
  font-style: normal;
  font-weight: 500;
  font-display: swap;
  src:
    url("../fonts/oswaldmedium.woff2") format("woff2"),
    url("../fonts/oswaldmedium.woff") format("woff");
}

:root {
  --accent-font: "Oswald", "Impact", sans-serif;
  --main-font: "Arial", "Helvetica", sans-serif;
}

:root {
  --main-text-color: #444444;
  --accent-text-color: #000000;
  --additional-text-color: #222222;
  --action-color: #ffffff;

  --main-bg-color: #68b738;
  --accent-bg-color: #f2f2f2;
}

.page {
  font-family: var(--main-font);
}

.page__body {
  margin: 0;
  color: var(--main-text-color);
}

.page__centerer {
  margin: 0 20px;

  &--nav-menu_mob-position {
    margin: 0;
  }

  &--form_padding {
    @media (--middle-vp) {
      padding: 0 40px;
    }
  }

  &--nav-menu_position {
    padding-top: 10px;

    @media (--middle-vp) {
      padding-top: 24px;
    }

    @media (--wide-vp) {
      padding-top: 53px;
    }
  }

  &--footer_position {
    display: flex;
    flex-direction: column;
  }

  @media (--middle-vp) {
    margin: 0 30px;
  }

  @media (--wide-vp) {
    margin: 0 auto;
    width: 1220px;
  }
}

@custom-media --small-vp (width <= 767.9px);

@custom-media --not-small-vp (width >= 768px);

@custom-media --middle-vp (768px <= width < 1279.9px);

@custom-media --wide-vp (width >= 1280px);

img {
  /*width: 100%;*/
  height: auto;
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}

.button {
  font-family: var(--accent-font);
  font-weight: 400;
  color: var(--action-color);
  background-color: var(--main-bg-color);
  width: fit-content;
  text-decoration: none;
  text-transform: uppercase;
  text-align: center;
  border: none;

  &--show-all_color {
    color: var(--main-text-color);
    background-color: var(--accent-bg-color);
  }
}

.field {
  border: none;
}

.field__contacts-legend {
  padding: 0;
  margin-bottom: 35px;
}

.field__extra-legend {
  padding: 0;
  margin-bottom: 34px;
}

.legend {
  font-family: var(--accent-font);
  font-weight: 400;
  font-size: var(--inner-title-size);
  line-height: 26px;
  color: var(--accent-text-color);
  display: block;

  @media (--not-small-vp) {
    font-size: var(--title-size);
    line-height: 36px;
  }
}

.label {
  font-family: var(--accent-font);
  font-weight: 400;
  font-size: 16px;
  line-height: 18px;
  color: var(--main-text-color);
  text-transform: uppercase;
  min-width: fit-content;
}

.input {
  width: 100%;
  box-sizing: border-box;
}

.input::placeholder {
  font-family: var(--accent-font);
  font-weight: 400;
  font-size: 16px;
  line-height: 18px;
  color: var(--main-text-color);
  text-transform: uppercase;
}

.title {
  --title-size: 36px;
  --title-big-size: 60px;
  --inner-title-size: 24px;

  font-family: var(--accent-font);
  font-weight: 400;
  color: var(--accent-text-color);
}

.description {
  --description-size: 14px;
  --description-size-large: 16px;

  font-weight: 400;
  font-size: var(--description-size);
  line-height: 18px;
}

.main-nav {
  position: relative;
  min-height: 65px;
  width: auto;

  &--color_reshuffle {
    @media (--wide-vp) {
      --reshuffle-color: var(--action-color);
    }
  }
}

// Временно, для отображения меню на странице.

// Позже заменю на svg ;)

.main-nav__toggle {
  content: "";
  position: absolute;
  top: 0;
  right: 20px;
  z-index: 2;
  width: 24px;
  height: 37px;
  background-color: var(--action-color);
  padding: 0;
  cursor: pointer;
  border: none;

  @media (--not-small-vp) {
    display: none;
  }
}

.main-nav--closed .main-nav__toggle::before {
  content: "";
  position: absolute;
  top: 13px;
  left: 0;
  width: 24px;
  height: 2px;
  background-color: var(--accent-text-color);
  box-shadow:
    0 7px 0 0 var(--accent-text-color),
    0 14px 0 0 var(--accent-text-color);
}

.main-nav--opened .main-nav__toggle {
  content: "";
  top: 0;
  right: 20px;

  &::before,
  &::after {
    content: "";
    position: absolute;
    top: 13px;
    left: 0;
    width: 24px;
    height: 2px;
    background-color: var(--accent-text-color);
  }

  &::before {
    transform: rotate(45deg);
    box-shadow: none;
  }

  &::after {
    transform: rotate(-45deg);
  }
}

@media (--small-vp) {
  .main-nav--closed .main-nav__list {
    display: none;
  }

  .main-nav--opened .main-nav__wrapper {
    position: absolute;
    top: 65px;
    right: 0;
    left: 0;
    z-index: 3;
    min-height: 50vh;
  }

  .main-nav--nojs .main-nav__toggle {
    display: none;
  }

  .main-nav--nojs .main-nav__wrapper {
    position: static;
    padding-top: 65px;
  }
}

.main-nav__list {
  padding: 0;
  margin: 0;
  list-style: none;
  background-color: var(--action-color);

  @media (--not-small-vp) {
    display: flex;
    flex-wrap: wrap;
    justify-content: flex-end;
    background-color: transparent;
  }
}

.main-nav__item {
  @media (--small-vp) {
    border-top: 1px solid #e6e6e6;

    &:last-child {
      border-bottom: 1px solid #e6e6e6;
    }
  }

  @media (--not-small-vp) {
    margin-right: 35px;

    &:last-child {
      margin-right: 0;
    }
  }
}

.main-nav__link {
  font-family: var(--accent-font);
  font-weight: 500;
  font-size: 20px;
  line-height: 1;
  color: var(--reshuffle-color, var(--accent-text-color));
  position: relative;
  display: block;
  padding: 22px 10px 24px;
  text-transform: uppercase;
  text-decoration: none;
  text-align: center;

  @media (--not-small-vp) {
    font-weight: 400;
    font-size: 18px;
    line-height: 24px;
  }

  @media (--wide-vp) {
    font-size: 20px;
    line-height: 30px;
  }
}

.offer {
  min-height: 480px;
  background:
    linear-gradient(
    rgba(104, 183, 56, 0.85) 0% 63%,
    #ffffff 63% 100%
    ),
    #4f4f54 url("../img/main_bg.jpg") no-repeat top;
  background-size: contain;
  position: relative;

  @media (--middle-vp) {
    background: var(--action-color);
    padding-left: 87px;
  }

  @media (--wide-vp) {
    position: static;
    display: grid;
    grid-template-columns: 1fr 1fr;
    padding-left: 80px;
    margin-bottom: 80px;
    background: none;
  }
}

.offer::before {
  position: absolute;
  content: "";
  background:
    linear-gradient(
    rgba(104, 183, 56, 0.85),
    rgba(104, 183, 56, 0.85)),
    #4f4f54 url("../img/main_bg.jpg") no-repeat top;
  background-size: contain;

  @media (--middle-vp) {
    display: none;
  }

  @media (--wide-vp) {
    top: 0;
    right: 0;
    min-height: 694px;
    background:
      linear-gradient(
      rgba(104, 183, 56, 0.85),
      rgba(104, 183, 56, 0.85)),
      #4f4e53 url("../img/main_bg.jpg") no-repeat;
    background-size: contain;
    background-position: top 0 left 180px;
    left: 50%;
    z-index: -1;
  }
}

.offer::after {
  position: absolute;
  content: "";
  top: 167px;
  left: 50%;
  transform: translateX(-50%);
  width: 270px;
  height: 280px;
  background: url("../img/catenergy_box.png") no-repeat;
  background-size: contain;

  @media (--middle-vp) {
    top: 255px;
    width: 709px;
    height: 675px;
    background-size: cover;
  }

  @media (--wide-vp) {
    position: relative;
    transform: translateX(0);
    width: 552px;
    height: 499px;
    top: 40px;
    left: -205px;
    background-size: cover;
  }
}

.offer__chapter {
  display: flex;
  flex-direction: column;
  min-height: 480px;
  margin-bottom: 20px;

  @media (--middle-vp) {
    min-height: 920px;
    order: 2;
    margin-bottom: 0;
  }

  @media (--wide-vp) {
    min-height: 553px;
  }
}

.offer__title {
  font-size: var(--title-size);
  line-height: 36px;
  color: var(--action-color);
  text-align: center;
  padding: 27px 0 0 0;
  margin: 0 0 25px 0;

  @media (--not-small-vp) {
    font-size: var(--title-big-size);
    line-height: 1;
    color: var(--accent-text-color);
    text-align: left;
    padding-top: 63px;
    margin-bottom: 41px;
  }

  @media (--wide-vp) {
    padding-top: 111px;
    margin-bottom: 38px;
  }
}

.offer__text {
  font-family: var(--accent-font);
  line-height: 1;
  color: var(--action-color);
  text-transform: uppercase;
  text-align: center;
  padding: 0;
  margin: 0 0 49px 0;

  @media (--not-small-vp) {
    font-size: 20px;
    line-height: 1;
    color: var(--accent-text-color);
    text-align: left;
  }

  @media (--wide-vp) {
    margin: 0 0 54px 0;
  }
}

.offer__button {
  font-size: 14px;
  line-height: 18px;
  text-align: center;
  min-width: 100%;
  padding: 11px 0;
  margin-top: auto;
  z-index: 1;

  @media (--not-small-vp) {
    font-size: 20px;
    min-width: 244px;
    padding: 14px 0;
    margin: 0;
  }
}

.options__list {
  list-style: none;
  padding: 0;
  margin: 0;

  @media (--middle-vp) {
    margin-bottom: 45px;
  }

  @media (--wide-vp) {
    display: grid;
    grid-template-columns: 1fr 1fr;
    gap: 0 80px;
    margin-bottom: 71px;
  }
}

.options__item {
  padding: 21px 33px 18px 19px;
  background-color: var(--accent-bg-color);
  margin-bottom: 20px;

  @media (--middle-vp) {
    padding: 77px 62px 57px 52px;
    margin-bottom: 30px;
  }

  @media (--wide-vp) {
    padding: 77px 51px 55px 52px;

    &--icon_tiger {
      padding-right: 61px;
    }
  }
}

.options__title {
  font-size: var(--inner-title-size);
  line-height: 37px;
  text-transform: uppercase;
  padding: 0 0 0 70px;
  margin: 0;
  margin-bottom: 29px;

  @media (--not-small-vp) {
    font-size: var(--title-size);
    line-height: 1;
    padding: 0;
    margin-bottom: 24px;
  }

  @media (--wide-vp) {
    padding-left: 162px;
    margin-bottom: 74px;
  }
}

.options__description {
  padding: 0;
  margin: 0;
  margin-bottom: 38px;
  position: relative;

  @media (--small-vp) {
    &::after {
      content: "";
      position: absolute;
      bottom: -23px;
      left: 0;
      width: 100%;
      height: 1px;
      background-color: #d9d9d9;
    }
  }

  @media (--not-small-vp) {
    font-size: var(--description-size-large);
    line-height: 24px;
    color: var(--accent-text-color);
    max-width: 340px;
    margin-bottom: 52px;
  }

  @media (--wide-vp) {
    max-width: 100%;
    margin-bottom: 23px;
  }
