.options__link {
  font-family: var(--accent-font);
  font-weight: 400;
  font-size: 16px;
  line-height: 1;
  color: var(--additional-text-color);
  text-transform: uppercase;
  text-decoration: none;

  @media (--not-small-vp) {
    font-size: 20px;
    line-height: 30px;
  }
}

.process__chapter {
  margin-bottom: 49px;

  @media (--middle-vp) {
    margin-bottom: 147px;
  }

  @media (--wide-vp) {
    margin-bottom: 126px;
  }
}

.process__title {
  font-size: var(--title-size);
  line-height: 40px;
  padding: 0;
  margin: 0;
  margin-bottom: 40px;

  @media (--not-small-vp) {
    font-size: var(--title-big-size);
    line-height: 1;
    margin-bottom: 72px;
  }
}

.process__list {
  list-style: none;
  padding: 0;
  margin: 0;

  @media (--middle-vp) {
    display: grid;
    grid-template-columns: 1fr 1fr;
    gap: 134px 60px;
  }

  @media (--wide-vp) {
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    gap: 0 80px;
  }
}

.process__item:not(:last-child) {
  margin-bottom: 20px;
}

.process__description {
  padding-left: 81px;
  margin: 0;

  @media (--not-small-vp) {
    font-size: var(--description-size-large);
    line-height: 24px;
    padding: 111px 0 0 0;
    max-width: 245px;
  }

  @media (--wide-vp) {
    max-width: 100%;
  }
}

.example {
  position: relative;
  background-color: #eaeaea;

  @media (--middle-vp) {
    background:
      linear-gradient(
        var(--action-color) 0 55%,
        #eaeaea 55% 100%
      );

    &::before {
      content: "";
      position: absolute;
      top: -43px;
      left: 0;
      width: 100%;
      height: 1px;
      background-color: #e6e6e6;
    }
  }

  @media (--wide-vp) {
    background:
      linear-gradient(
        var(--action-color) 0 22%,
        #f2f2f2 22% 100%
      );
  }
}

.example__chapter {
  @media (--small-vp) {
    padding: 25px 0 40px;
  }

  @media (--wide-vp) {
    display: grid;
    grid-template-columns: 1fr 1fr;
    gap: 0 80px;
    padding-bottom: 70px;
  }
}

.example__title {
  font-size: var(--title-size);
  line-height: 40px;
  padding: 0;
  margin: 0;
  margin-bottom: 40px;

  @media (--not-small-vp) {
    font-size: var(--title-big-size);
    line-height: 1;
    margin-bottom: 63px;
  }

  @media (--wide-vp) {
    margin-bottom: 143px;
  }
}

.example__description {
  margin: 0 0 35px 0;

  @media (--not-small-vp) {
    font-size: var(--description-size-large);
    line-height: 24px;
    margin-bottom: 54px;
  }

  @media (--wide-vp) {
    max-width: 436px;
    margin-bottom: 72px;
