}

.example__list {
  margin: 0;
}

.stats {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;

  @media (--not-small-vp) {
    justify-content: flex-start;
  }
}

.stats__item {
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 125px;
  margin-bottom: 20px;
  position: relative;
  z-index: 0;

  &--border::before {
    content: "";
    position: absolute;
    left: 0;
    width: 124px;
    height: 56px;
    border: 1px solid #cdcdcd;
    border-radius: 3px;
    z-index: -1;

    @media (--not-small-vp) {
