  }

  @media (--wide-vp) {
    background-color: #f2f2f2;
  }

  &--text_feature {
    font-family: var(--accent-font);
    font-size: 14px;
    line-height: 20px;
    color: var(--accent-text-color);
    text-transform: uppercase;
    max-width: fit-content;
    order: -1;
    margin-right: 5px;

    @media (--not-small-vp) {
      font-size: 20px;
      line-height: 1;
      padding: 0;
      margin: 0;
    }

    &:first-child {
      @media (--middle-vp) {
        margin-bottom: 6px;
      }
    }

    &:not(:last-child) {
      @media (--wide-vp) {
        margin-right: 43px;
      }
    }
  }
}

.stats__number {
  font-family: var(--accent-font);
  font-weight: 400;
  font-size: 24px;
  line-height: 24px;
  color: var(--accent-text-color);
