  }
}

.questionnaire__chapter {
  margin-bottom: 32px;
}

.questionnaire__title {
  font-size: var(--title-size);
  line-height: 36px;
  padding: 0;
  margin: 0;
  margin-bottom: 37px;
}

.questionnaire__description {
  font-family: var(--accent-font);
  color: var(--accent-text-color);
