  }
}

.form__contacts {
  margin-bottom: 23px;

  @media (--middle-vp) {
    margin-bottom: 65px;
  }
}

.form__extra {
  margin-bottom: 34px;
}

.form__button {
  width: 100%;
  padding: 0;
  margin: 0;
  margin-bottom: 21px;

  @media (--not-small-vp) {
    grid-column: 1 / 3;
  }
}

.form__result {
  @media (--not-small-vp) {
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    gap: 0 60px;
  }
}

.form__footnote {
  font-weight: 400;
  font-size: 14px;
  line-height: 16px;
  text-align: center;
  margin-bottom: 32px;

  @media (--not-small-vp) {
    grid-column: 3 / 5;
    text-align: left;
  }
}

.general-list__item {
  display: flex;
}

.general-list__item:not(:last-child) {
  margin-bottom: 12px;
}

.general-list__label {
  margin-right: 13px;
}

.choice-list__item:not(:last-child) {
  margin-bottom: 12px;
}

.choice-list__label {
  color: var(--additional-text-color);
}

.contacts-list {
  @media (--wide-vp) {
    display: grid;
    grid-template-columns: 1fr 1fr;
    gap: 0 80px;
  }
}

.contacts-list__item {
  display: flex;
}

.contacts-list__item:not(:last-child) {
  margin-bottom: 14px;
}

.contacts-list__label {
  margin-right: 14px;
}

.extra-list {
  @media (--not-small-vp) {
    display: grid;
    grid-template-columns: 1fr 1fr;
    gap: 32px 60px;
  }

  @media (--wide-vp) {
    grid-template-columns: repeat(4, 1fr);
    gap: 0 80px;
  }
}

.extra-list__item:not(:last-child) {
  margin-bottom: 12px;
}

.extra-list__label {
  color: var(--accent-text-color);

  @media (--not-small-vp) {
    font-size: 20px;
    line-height: 30px;
  }
}

.comment__chapter {
  margin-bottom: 27px;
}

.comment__legend {
  margin-bottom: 38px;
}

.main-inner__title {
  font-size: var(--title-size);
  line-height: 36px;
  padding: 0;
  margin: 0 0 49px 0;
}

.catalog__table {
  display: grid;
  margin-bottom: 50px;
}

.catalog__button {
  display: block;
  width: 100%;
}

.table {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(245px, 1fr));
  gap: 40px;

  @media (--middle-vp) {
    gap: 60px;
  }

  @media (--wide-vp) {
    gap: 80px;
  }
}

.table__line {
  display: grid;
  grid-template-columns: 1fr 1fr;
  margin-bottom: 33px;

  &--show_more {
    grid-template-columns: 1fr;
  }

  @media (--not-small-vp) {
    grid-template-columns: 1fr;
    padding: 0 36px 39px;
  }
}

.table__line:last-child {
  margin-top: 50px;
}

.table__title {
  padding: 0;
  margin: 0;
  margin-bottom: 14px;

  @media (--not-small-vp) {
    grid-row: 2/3;
  }
}

.table__link {
  font-family: var(--accent-font);
  font-weight: 400;
  font-size: 16px;
  line-height: 20px;
  color: var(--additional-text-color);
  display: block;
  text-decoration: none;
  text-transform: uppercase;

  &--show_position {
    text-align: center;
    margin-bottom: 8px;
  }
}

.table__cell {
  font-weight: 400;
  font-size: 12px;
  line-height: 16px;
  padding: 0;
  margin: 0;

  &--text_position {
    text-align: center;
    margin-bottom: 21px;
  }
}

.table__image {
  display: block;
  grid-area: 1 / 1 / 5 / 2;

  @media (--not-small-vp) {
    grid-row: 1 / 2;
    text-align: center;
  }
}

.table__image-link {
  display: block;
}

.table__button {
  grid-column: 1 / -1;
}

.add-goods__table {
  margin-bottom: 54px;
}

.add-goods__title {
  font-size: var(--inner-title-size);
  line-height: 24px;
  padding: 0;
  margin: 0;
  margin-bottom: 49px;
}

.add-goods__wrapper {
  @media (--wide-vp) {
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    gap: 0 80px;
  }
}

.add-goods__title {
  @media (--not-small-vp) {
    font-size: var(--title-size);
    margin-bottom: 80px;
  }
}

.add-goods__table {
  display: grid;
  grid-template-columns: 1fr;

  @media (--wide-vp) {
    grid-column: 1 / 4;
    margin-bottom: 0;
  }
}

.add-table__line {
  display: grid;
  grid-template-columns: 1fr 1fr;
  margin-bottom: 31px;

  @media (--middle-vp) {
    grid-template-columns: repeat(4, 1fr);
    gap: 0 60px;
    padding: 0 37px;
  }

  @media (--wide-vp) {
    grid-template-columns: 325px 245px 80px 245px;
    width: 895px;
  }
}

.add-table__title {
  font-family: var(--accent-font);
  font-weight: 400;
  font-size: 16px;
  line-height: 16px;
  color: var(--additional-text-color);
  text-transform: uppercase;
  margin-bottom: 13px;
  grid-column: 1 / 3;
  text-align: left;

  @media (--wide-vp) {
    grid-column: 1 / 2;
  }
}

.add-table__cell {
  font-weight: 400;
  font-size: var(--description-size);
  line-height: 14px;
  margin-bottom: 15px;

  &--priсe {
    text-align: right;

    @media (--wide-vp) {
      text-align: left;
    }
  }
}

.add-table__cell--volume {
  @media (--middle-vp) {
    text-align: right;
    margin-right: -80px;
  }

  @media (--wide-vp) {
    text-align: left;
  }
}

.add-table__button {
  grid-column: 1 / -1;
  margin: 0;

  @media (--wide-vp) {
    grid-column: 4 / 5;
  }
}

.add-goods__button {
  display: block;
  width: 100%;
  padding: 10px 0;
}

.present {
  min-height: 290px;
  position: relative;

  @media (--wide-vp) {
    grid-column: 4 / 5;
  }
}

.present::before {
  position: absolute;
  content: "";
  width: 100%;
  min-height: 290px;
  background:
    linear-gradient(
    rgba(104, 183, 56, 0.85),
    rgba(104, 183, 56, 0.85)),
    url("../img/cat_case.jpg") no-repeat center;
  background-size: cover;
  z-index: -1;
}

.present__chapter {
  margin-bottom: 35px;
}

.present__text {
  font-weight: 400;
  font-size: 16px;
  line-height: 20px;
  color: var(--action-color);
  text-align: center;
  max-width: 161px;
  padding: 0;
  margin: 0;
}
